import React from 'react'
import ReactHTMLParser from 'react-html-parser'
import styled from 'styled-components'
import { Remarkable } from 'remarkable'
import { LazyLoadImage } from 'react-lazy-load-image-component'
import 'react-lazy-load-image-component/src/effects/blur.css'
import { FaLinkedin, FaTwitter, FaFacebookF } from 'react-icons/fa'
import { ImageContainer, SocialIcon } from './style'
import { TextContent } from './../../../components'
import { useLocation } from '@reach/router'

interface IPersonProps {
  image: any
  name: string
  descriptionEn: string
  descriptionFr: string
  links: { title: string; url: string }[]
  social: { type: string; url: string }[]
}

const Content = styled.div`
  font-size: 22px;
  line-height: 34px;
  color: ${props => props.theme.palette.primary};
  a {
    font-size: 22px;
    line-height: 32px;
    color: ${props => props.theme.palette.darkSecondary};
    position: relative;
    text-decoration: none;
    display: inline-block;

    &:before {
      content: '';
      position: absolute;
      width: 0%;
      height: 4px;
      bottom: 0px;
      left: 0;
      background-color: ${props => props.theme.palette.darkSecondary};
      visibility: hidden;
      transition: all 0.5s ease-in-out 0s;
    }
    &:hover::before {
      width: 100%;
      visibility: visible;
    }
    &::hover {
      color: ${props => props.theme.palette.darkSecondary};
      text-decoration: none;
    }
  }
`

export default ({
  image,
  name,
  descriptionEn,
  descriptionFr,
  links = [],
  social = [],
}: IPersonProps) => {
  const md = new Remarkable()

  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  let contentEn = md.render(descriptionEn).replace(/<\a/g, '<a target="_blank" ')
  let contentFr = md.render(descriptionFr).replace(/<\a/g, '<a target="_blank" ')

  return (
    <div>
      <ImageContainer>
        <LazyLoadImage
          src={image.publicURL}
          height='100%'
          width='100%'
          style={{ objectFit: 'cover' }}
          alt={name}
          effect='blur'
        />
      </ImageContainer>
      <h2 className='mt-3'>{name}</h2>
      <Content className='mt-4'>
      {isFr ? 
        ReactHTMLParser(contentFr)
       : 
        ReactHTMLParser(contentEn)
      }
      </Content>
      <TextContent>
        <div className='mt-5 mb-3'>
          {isFr ? (
            <b>{`Pour en savoir plus sur ${name
              .split(' ')
              .slice(0, 1)
              .join(' ')}, clique sur les liens ci-dessous:`}</b>
          ) : (
            <b>{`For more on ${name
              .split(' ')
              .slice(0, 1)
              .join(' ')}, click the links below:`}</b>
          )}
        </div>
        {links &&
          links.map(({ title, url }) => {
            return (
              <a
                href={url}
                target='_blank'
                className='d-block'
                rel='noreferrer'
                key={title}
              >
                <span style={{ wordBreak: 'break-all' }}>{title}</span>
              </a>
            )
          })}
        <div className='d-flex mt-3'>
          {social &&
            social.map(({ type, url }) => {
              return type === 'linkedin' ? (
                <SocialIcon
                  href={url}
                  target='_blank'
                  rel='noreferrer'
                  key={url}
                >
                  <FaLinkedin size={36} />
                </SocialIcon>
              ) : type === 'facebook' ? (
                <SocialIcon
                  href={url}
                  target='_blank'
                  rel='noreferrer'
                  key={url}
                >
                  <FaFacebookF size={36} />
                </SocialIcon>
              ) : type === 'twitter' ? (
                <SocialIcon
                  href={url}
                  target='_blank'
                  rel='noreferrer'
                  key={url}
                >
                  <FaTwitter size={36} />
                </SocialIcon>
              ) : null
            })}
        </div>
      </TextContent>
    </div>
  )
}
