import * as React from "react";
import { graphql } from "gatsby";
import Layout from "../layout";
import { SEO, StripeBar, Banner, Button, SpotlightFooter } from "../components";
import { Person, Checkout } from "../containers/Spotlight";
import { Content } from "../containers/Spotlight/style";
import { useLocation } from "@reach/router";

interface PastSpotlightPageProps {
  location: {
    pathname: string;
  };
  pageContext: {
    data: any;
    seoKeyword: string;
    seoDescription: string;
  };
}

export default ({
  location,
  pageContext: { data, seoKeyword, seoDescription },
}: PastSpotlightPageProps) => {
  const loc = useLocation();
  const isFr = loc.pathname.startsWith("/fr/");

  let pastSpotlights = data.filter((item: any) => {
    return !item.featuredSpotlight ? true : false;
  });

  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        title={isFr ? "En vedette dans le passé" : "Past Spotlights"}
        keyword={seoKeyword}
        description={seoDescription}
      />
      <Banner colors={["#397897", "#00587F", "#80D0DB"]}>
        <h1>{isFr ? "En vedette dans le passé" : "Past Spotlights"} </h1>
      </Banner>
      <StripeBar colors={["#D16428", "#E58630", "#DA5027", "#D29329"]} />
      <Content>
        <div className="container">
          <div className="d-flex flex-wrap">
            {pastSpotlights.length === 0 ? (
              <div className="row" style={{ width: "100%" }}>
                <div
                  className="col-md-12 mb-12 d-flex"
                  style={{
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <h2 className="text-center mb-5">
                    {isFr
                      ? `Revenez plus tard pour voir d'autres biographies présentées dans le passé.`
                      : "Check back later for more biographies featured in the past."}
                  </h2>
                  <Button className="" type="url" to="/spotlight">
                    {isFr
                      ? "Voir nos biographies en vedette"
                      : "View Our Featured Biographies"}
                  </Button>
                </div>
              </div>
            ) : (
              <div className="row">
                {pastSpotlights.map((item: any) => {
                  return (
                    <div className="col-md-6 mb-5" key={item.name}>
                      <Person {...item} />
                    </div>
                  );
                })}
              </div>
            )}
          </div>
        </div>
      </Content>
      <SpotlightFooter>
        <h2>
          {isFr
            ? "Présente ta propre histoire pour nous faire part de ce que tu fais pour défendre l’égalité des genres! Et découvre d’autres défenseurs et leur rôle dans la lutte pour l’égalité des sexes. "
            : "Submit your own story to tell us what you’re doing to advocate for gender equality! And check out other advocates and their roll in gender equality"}
        </h2>
        <div className="mt-5">
          <div className="d-block d-md-flex">
            <Button type="url" to="/submitStory">
              {isFr ? "Présente ton histoire" : "Submit Your Story"}
            </Button>
            <Button
              className="ml-0 mt-4 mt-md-0 ml-md-5"
              type="url"
              to="/stories"
            >
              {isFr
                ? "Voir d’autres histoires de défenseurs"
                : "View Other Advocate Stories"}
            </Button>
          </div>
        </div>
      </SpotlightFooter>
    </Layout>
  );
};

export const pastSpotlightPageQuery = graphql`
  query PastSpotlightPageQuery {
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`;
